const refs = {
  btnClose: document.querySelector('.location-modal-window__close-btn'), //     /
  modal: document.querySelector('[data-modal="location"]'), //    /
  setCity: document.querySelector('.location-btn__city'), //  
  setLanguage: document.querySelector('.location-btn__language'), //  
  buttonsLanguage: document.querySelectorAll('.location-modal__language-btn'), //     
  buttonsCity: document.querySelectorAll('.location-modal__city-btn'), //     
  selectLanguage: document.querySelector('.location-modal__language'), //    
  selectCity: document.querySelector('.location-modal__city'), //    
};

const selectLanguage = () => {
  refs.buttonsLanguage.forEach(button => {
    button.addEventListener('click', () => {
      refs.buttonsLanguage.forEach(btn => btn.classList.remove('selected'));
      button.classList.add('selected');
    });
  });
};

const selectCity = () => {
  refs.buttonsCity.forEach(button => {
    button.addEventListener('click', () => {
      refs.buttonsCity.forEach(btn => btn.classList.remove('selected'));
      button.classList.add('selected');
    });
  });
};

function onChangeLanguageClick(e) {
  const language = e.target;
  if (language.nodeName === 'BUTTON') {
    refs.setLanguage.textContent = language.outerText;
  }
}

function onChangeCityClick(e) {
  const city = e.target;
  if (city.nodeName === 'BUTTON') {
    refs.setCity.textContent = city.outerText;
  }
}

export function openModalLocation() {
  refs.modal.classList.remove('is-hidden');

  //      
  refs.btnClose.addEventListener('click', closeModalLocation);

  //              
  refs.selectLanguage.addEventListener('click', onChangeLanguageClick);
  refs.selectCity.addEventListener('click', onChangeCityClick);

  selectLanguage(); //       
  selectCity(); //       
}

function closeModalLocation() {
  refs.modal.classList.add('is-hidden');
}
